.services {
    background-color: $black;
    width: 100%;
    max-width: 1256px;
    padding: 60px 20px;
    margin: 0 auto;
    h2 {
        color: $white;
        font-family: $font;
        font-weight: 400;
        font-size: 36px;
        @media (min-width: $m) {
            text-align: center; } }
    &__list {
        color: $services-text;
        margin: 10px 0;
        font-family: $font;
        line-height: 26px;

        list-style-type: none;
        li {
            padding-left: 20px;
            position: relative;
            &::before {
                content: '•';
                font-size: 16px;
                font-weight: bold;
                position: absolute;
                left: 0px; } } }
    &__flex-container {
        display: flex;
        flex-flow: column;
        @media (min-width: $m) {
            flex-flow: row;
            justify-content: space-around;
            margin: 60px 0; } } }
