.footer {
    background-color: $black;
    color: $white;
    min-height: 285px;
    padding: 10px 20px;
    &__social {
        margin: 95px 0;
        text-align: center;
        h3 {
            color: $footer-heading;
            font-family: $font;
            font-weight: 500;
            font-size: 30px;
            margin-bottom: 10px;
            @media (min-width: $m) {
                margin-bottom: 15px; } } }
    &__social-links {
        display: flex;
        margin: 0 auto;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width:  205px;
        @media (min-width: $m) {
            max-width: 405px; } }

    &__social-link {
        display: inline-block;
        margin: 10px 0px;
        width: 24px;
        flex-basis: 33%;
        @media (min-width: $m) {
            flex-basis: initial; }
        img {
            height: 24px;
            width: 100%; } }
    &__newsletter-form {
        input {
            background-color: $black;
            color: $form-border;
            min-width: 208px;
            margin-right: 16px;
            &::placeholder {
                color: $form-border; } }
        button {
            margin: 10px 0; } } }


