@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

body {
  display: flex;
  flex-flow: column; }
  body.overflow-hidden {
    overflow-y: hidden; }

form input {
  height: 32px; }
  @media (min-width: 940px) {
    form input {
      height: 46px; } }

form textarea {
  height: 171px; }
  @media (min-width: 940px) {
    form textarea {
      height: 232px; } }

form input,
form textarea {
  border: 1px solid #979797;
  margin: 10px 0;
  resize: none;
  padding: 10px 14px; }
  form input::placeholder,
  form textarea::placeholder {
    color: #303030;
    font-family: "Raleway", sans-serif;
    font-weight: 300; }
  form input:focus,
  form textarea:focus {
    outline-color: #a1baf4; }

form.submitted input:invalid,
form.submitted textarea:invalid {
  border-color: #f08992; }
  form.submitted input:invalid:focus,
  form.submitted textarea:invalid:focus {
    outline-color: #f08992; }

.link {
  color: #000;
  text-transform: uppercase;
  font-size: 15px;
  font-family: "Raleway", sans-serif;
  display: block;
  padding: 10px 0; }

.header {
  position: relative;
  width: 100%; }

.page-nav {
  position: absolute;
  top: 0;
  left: 0;
  padding: 15px 20px;
  color: #FFF;
  background-color: transparent;
  font-family: "Raleway", sans-serif; }
  .page-nav__expansion-container {
    display: none;
    height: 100vh; }
  .page-nav__menu-control {
    padding: 10px;
    font-size: 16px; }
  .page-nav__menu-icon {
    vertical-align: sub;
    font-size: 20px; }
  .page-nav__menu {
    list-style-type: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: calc(100% - 40px); }
  .page-nav__nav-item {
    margin: 12px 0;
    width: 100%; }
  .page-nav__nav-link {
    text-align: center;
    display: block;
    padding: 10px;
    font-size: 30px;
    text-decoration: none;
    color: inherit; }
  .page-nav--opened {
    width: 100%;
    display: block;
    background-color: #1e1e1e;
    z-index: 2; }
    .page-nav--opened .page-nav__expansion-container {
      display: block; }
    .page-nav--opened #menu-open {
      display: none; }

.slider {
  height: 640px;
  width: 100%;
  background-color: #414141; }

.contact {
  width: 100%;
  background-color: #FFF;
  padding: 60px 20px;
  max-width: 940px;
  margin: 0 auto; }
  .contact__info h1 {
    font-size: 36px;
    color: #464646;
    margin-bottom: 10px;
    font-family: "Raleway-Medium", sans-serif;
    font-weight: 400; }
  .contact__info p {
    color: #414141;
    font-size: 16px;
    font-family: "Raleway", sans-serif;
    margin: 0; }
  @media (min-width: 940px) {
    .contact__info {
      text-align: center; } }
  .contact__flex-container {
    display: flex;
    flex-flow: column; }
    @media (min-width: 940px) {
      .contact__flex-container {
        flex-flow: row;
        padding: 60px 80px; } }
  .contact__general-info {
    font-family: "Lato", sans-serif;
    font-weight: 500;
    padding: 30px 0;
    font-size: 15px;
    font-weight: 500;
    width: 300px; }
  .contact__table {
    width: 100%;
    border: none;
    margin: 40px 0; }
    .contact__table tr td:first-child {
      font-size: 12px;
      font-family: "Raleway", sans-serif;
      padding-right: 30px;
      text-transform: uppercase;
      line-height: 2.08;
      color: #3d3d3d; }
    .contact__table tr td:not(:first-child) {
      font-family: "Lato", sans-serif;
      font-weight: 500;
      color: #323232; }
    .contact__table tr td a {
      text-decoration: none;
      color: inherit; }
    @media (min-width: 940px) {
      .contact__table {
        margin-right: 125px; } }
  .contact__form {
    display: flex;
    flex-flow: column;
    align-items: flex-end;
    width: 100%; }
  .contact__form-element {
    width: 100%;
    border: 1px solid #979797;
    margin: 10px 0;
    min-height: 32px;
    resize: none;
    padding: 10px 14px; }
  .contact__validation-message--success {
    color: #53bc54; }
  .contact__validation-message--error {
    color: #f81010; }

.services {
  background-color: #000;
  width: 100%;
  max-width: 1256px;
  padding: 60px 20px;
  margin: 0 auto; }
  .services h2 {
    color: #FFF;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-size: 36px; }
    @media (min-width: 940px) {
      .services h2 {
        text-align: center; } }
  .services__list {
    color: #acacac;
    margin: 10px 0;
    font-family: "Raleway", sans-serif;
    line-height: 26px;
    list-style-type: none; }
    .services__list li {
      padding-left: 20px;
      position: relative; }
      .services__list li::before {
        content: '•';
        font-size: 16px;
        font-weight: bold;
        position: absolute;
        left: 0px; }
  .services__flex-container {
    display: flex;
    flex-flow: column; }
    @media (min-width: 940px) {
      .services__flex-container {
        flex-flow: row;
        justify-content: space-around;
        margin: 60px 0; } }

.footer {
  background-color: #000;
  color: #FFF;
  min-height: 285px;
  padding: 10px 20px; }
  .footer__social {
    margin: 95px 0;
    text-align: center; }
    .footer__social h3 {
      color: #7a7a7a;
      font-family: "Raleway", sans-serif;
      font-weight: 500;
      font-size: 30px;
      margin-bottom: 10px; }
      @media (min-width: 940px) {
        .footer__social h3 {
          margin-bottom: 15px; } }
  .footer__social-links {
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 205px; }
    @media (min-width: 940px) {
      .footer__social-links {
        max-width: 405px; } }
  .footer__social-link {
    display: inline-block;
    margin: 10px 0px;
    width: 24px;
    flex-basis: 33%; }
    @media (min-width: 940px) {
      .footer__social-link {
        flex-basis: initial; } }
    .footer__social-link img {
      height: 24px;
      width: 100%; }
  .footer__newsletter-form input {
    background-color: #000;
    color: #979797;
    min-width: 208px;
    margin-right: 16px; }
    .footer__newsletter-form input::placeholder {
      color: #979797; }
  .footer__newsletter-form button {
    margin: 10px 0; }

.btn {
  border: none;
  border-radius: 0;
  font-family: "Raleway", sans-serif; }
  .btn:not([disabled]):hover {
    background-color: #a1baf4;
    color: #FFF; }
  .btn--primary {
    background-color: #FFF;
    color: #000; }
  .btn--secondary {
    background-color: #000;
    color: #FFF; }
  .btn--big {
    padding-left: 24px;
    padding-right: 24px;
    height: 44px; }
    @media (min-width: 940px) {
      .btn--big {
        height: 50px; } }
  .btn--medium {
    min-width: 93px;
    padding-left: 10px;
    padding-right: 10px;
    height: 32px; }
    @media (min-width: 940px) {
      .btn--medium {
        height: 50px; } }

@keyframes animate-from-left {
  from {
    transform: translate(-150%); }
  to {
    transform: translate(0%); } }

@keyframes animate-from-right {
  from {
    transform: translate(-150%); }
  to {
    transform: translate(0%); } }

.article {
  margin: 0 auto;
  width: 100%;
  padding: 30px 20px; }
  @media (min-width: 940px) {
    .article {
      padding: 120px 20px; } }
  .article__flex-container {
    display: flex;
    flex-flow: column;
    max-width: 940px;
    width: 100%; }
    @media (min-width: 940px) {
      .article__flex-container {
        flex-flow: row;
        justify-content: space-between;
        margin: 0 auto; } }
    .article__flex-container--left {
      flex-flow: row-reverse; }
  .article:nth-of-type(odd) {
    background-color: #FFF; }
  .article:nth-of-type(even) {
    background-color: #f8f8f8; }
  .article__image-container {
    position: relative;
    display: block; }
    @media (min-width: 940px) {
      .article__image-container {
        height: 299px; } }
  .article__image {
    width: 100%; }
    @media (min-width: 940px) {
      .article__image {
        height: 100%;
        width: 540px; } }
  .article__text-container {
    display: flex;
    flex-flow: column;
    justify-content: space-around; }
    .article__text-container h3 {
      font-family: "Raleway-Medium", sans-serif;
      font-size: 24px;
      color: #464646;
      margin: 20px 0 10px; }
    .article__text-container p {
      line-height: 24px;
      size: 16px;
      font-family: "Raleway", sans-serif;
      color: #414141;
      margin-bottom: 10px; }
    .article__text-container a {
      font-weight: 700;
      color: #464646; }
    @media (min-width: 940px) {
      .article__text-container {
        padding-left: 60px; } }
